import React, { Component } from "react";
import NewsItem from "./NewsItem";
import PropTypes from 'prop-types'


export class News extends Component {
    
   static defaultProps={
     country:  'in',
     pageSize:  8,
     category: 'general',
   }
   static propTypes={
     country:   PropTypes.string,
     pageSize:  PropTypes.number,
     category:  PropTypes.string

   }

  constructor() {
    super();
    console.log("vinod ");
      this.state = {
      articles:[],
      loading: false,
      page:1
    };
  }

   async componentDidMount()
  {
    
    let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=a1c148281f14462a9e84b736585aa430&page=1&pageSize=${this.props.pageSize}`;
    let data=await fetch(url);
    let parseData=await data.json()
    console.log(parseData);
    this.setState({articles:parseData.articles,totalResults: parseData.totalResults})
  }
      

   handlePrevClick=async()=>{
    console.log("vinod");
    let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=a1c148281f14462a9e84b736585aa430&page= ${this.state.page-1}&pageSize=${this.props.pageSize}`;
    let data=await fetch(url);
    let parseData=await data.json()
    console.log(parseData);
    this.setState({
      page:this.state.page-1,
      articles:parseData.articles
    })
  } 

 
  handleNextClick=async()=>{
    console.log("Manoj");
     if(this.state.page+1>Math.ceil(this.state.totalResults/this.props.pageSize))
     {

     }
     else
     {
    let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=a1c148281f14462a9e84b736585aa430&page=${this.state.page+1}&pageSize=${this.props.pageSize}`; 
    let data=await fetch(url);
    let parseData=await data.json()
    console.log(parseData);
    this.setState({
      page:this.state.page+1,
      articles:parseData.articles,
    })
  }
  }
  


  render() {
    return (   
      <div className="container my-3">
       <h1 className="text-center my-3">DailyNews - Indian Express</h1>
      
        <div className="row">
          {this.state.articles.map((element) => {
            
            return <div className="col-md-4" key={element.url}>
                <NewsItem
                  title={element.title?element.title:""}
                  description={element.description?element.description:""}
                  imageurl={element.urlToImage}
                  newsUrl={element.url}
                  author={element.author}
                  date={element.publishedAt}
                />
              </div>
            
            
          })}
         
          
      </div>
        <div className="container d-flex justify-content-between">
        <button disabled={this.state.page<=1} type="button" className="btn btn-success" onClick={this.handlePrevClick}>&larr; Previous</button>
        <button disabled={this.state.page+1>Math.ceil(this.state.totalResults/this.props.pageSize)} type="button" className="btn btn-success" onClick={this.handleNextClick}>Next &rarr;</button>
        </div>
      </div>
      
    )
  }
}


export default News;
